@use "../../abstract" as *;

.blog-block{
    z-index: 1;
    .shape-1{
        position: absolute;
        top: 0;
        left: 0;
        z-index:1;
        width: 18.5%;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .shape-2{
        position: absolute;
        bottom: 15%;
        right: 5%;
        z-index:1;
        width: 39px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

/* blog-card */
.blog-card{
    position: relative;
    z-index: 1;
    background-color: $white;
    border-radius: 5px;
    &__image{
        width: 100%;
        height: 270px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content{
        padding: 24px 20px;
        padding-bottom: 10px;
        position: relative;
        z-index: 1;
        &__title{
            font-size: 22px;
            font-weight: 600;
            line-height: 2rem;
            color: $heading-color;
            margin-bottom: 10px;
        }
        &__text,
        &__text p{
            font-size: 17px;
            font-weight: 400;
            line-height: 29px;
            margin-bottom: 15px;
        }
     .btn-link{
        font-size: 14px;
        color: $heading-color;
        font-weight: 700;
        text-transform: uppercase;
        &:hover{
            color: $primary;
        }
        &.border-bottom{
           border-bottom: 2px solid $primary !important;
        }
     }
    }
    &__meta-bottom{
        color: $regent-gray;
        font-size: 15px;
        span{
            margin-left: 5px;
            font-weight: 500;
            color: $regent-gray;
            font-size: 15px;
        }
    }
}