@use "../../abstract" as *;
.map-section{
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;

    .google-map{
        box-shadow: 0 4px 80px 0 rgba(0, 0, 0, 0.06);
        padding: 15px;
        background-color: #fff;
        width: 100%;
        height: 100%;
    }
    .shape{
        position: absolute;
        top: -7%;
        left: -7%;
        z-index: -1;
        width: 55%;
        img{
            width: 100%;
            height: 100%;
            overflow: hidden;
            object-fit: contain;
        }
    }
}

.contact-form-block{
    position: relative;
    z-index: 1;
    .shape-1{
        position: absolute;
        top: 10%;
        left: 0%;
        z-index: -1;
        width: 5%;
        img{
            width: 100%;
            height: 100%;
            overflow: hidden;
            object-fit: contain;
        }
    }
    &__inner{
        padding-left: 50px;
        .form-control{
            border-radius: 5px;
            border-color: $geyser;
            background-color: $polar;
        }
    }
}